
  import { UserOutlined, LockOutlined } from "@ant-design/icons-vue"
  import { defineComponent, reactive } from "vue"

  export default defineComponent({
    components: {
      UserOutlined,
      LockOutlined,
    },
    emits: ["login"],
    setup(props, { emit }) {
      const formState = reactive({
        username: "",
        password: "",
      })

      // 提交登录事件
      const handleFinish = (): void => {
        emit("login", formState)
      }

      return {
        formState,
        handleFinish,
      }
    },
  })
